:root {
  --font-family: "Roboto", sans-serif;
  --content-width: 1397px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light-color: #fff;
  --black-color: #000;
  --accent-color: #FB4B4D;
  --text-color: #222;
}

/* stylelint-disable */ /* stylelint-disable */ /* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable */ /* stylelint-disable */ /* stylelint-disable value-keyword-case */
@font-face {
  font-family: "Muller";
  src: url("../fonts/../fonts/RobotoRegular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  min-width: 375px;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}
@media (max-width: 1440px) {
  .site-container {
    min-width: 1430px;
  }
}
@media (max-width: 1024px) {
  .site-container {
    min-width: auto;
  }
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.flex {
  display: flex;
}
@media (max-width: 1024px) {
  .flex {
    flex-direction: column;
    align-items: center;
  }
}

.header__block {
  align-items: center;
  justify-content: space-between;
  padding: 22px;
}
@media (max-width: 1024px) {
  .header__block {
    flex-direction: column;
  }
}

.btn {
  display: inline-block;
  border: none;
  padding: 15px 44px;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 0.5px;
  color: var(--light-color);
  background-color: var(--accent-color);
  cursor: pointer;
}

.border-block {
  border: 1px solid var(--black-color);
}

.nav__list {
  display: flex;
  align-items: center;
}
@media (max-width: 1024px) {
  .nav__list {
    flex-direction: column;
  }
}
.nav__item:not(:last-child) {
  margin-right: 30px;
}
@media (max-width: 1024px) {
  .nav__item:not(:last-child) {
    margin: 0;
  }
}
.nav__link {
  display: inline-block;
  padding: 15px 44px;
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: var(--text-color);
}

.logo {
  padding: 0;
}

.hero__block {
  margin-top: -1px;
}
@media (max-width: 1024px) {
  .hero__ujjo {
    display: flex;
    justify-content: center;
  }
}
.hero__left {
  border-right: 1px solid var(--black-color);
  width: 50%;
}
@media (max-width: 1024px) {
  .hero__left {
    border: none;
    width: 100%;
  }
}
.hero__right {
  width: 50%;
}
@media (max-width: 1024px) {
  .hero__right {
    border: none;
    width: 100%;
  }
}
.hero__phrase {
  padding: 29px 0;
  font-weight: 400;
  font-size: 17px;
  line-height: 26px;
  text-align: center;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: var(--black-color);
}
.hero__image {
  padding: 20px;
}
.hero__image img {
  width: 100%;
  object-fit: cover;
}
.hero__title {
  margin: 0;
  border-bottom: 1px solid var(--black-color);
  padding: 20px 100px;
  text-align: center;
  font-weight: 400;
  font-size: 58px;
  line-height: 65px;
  text-align: center;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: var(--black-color);
}
@media (max-width: 768px) {
  .hero__title {
    padding: 20px;
    font-size: 45px;
    line-height: 45px;
  }
}
.hero__image-2 {
  padding: 20px;
}
.hero__image-2 img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.ticker {
  position: relative;
  overflow: hidden;
  border-top: 1px solid var(--black-color);
  border-bottom: 1px solid var(--black-color);
}

.ticker__wrapper {
  display: flex;
}

.ticker__item {
  display: flex;
  flex-shrink: 0;
  align-items: center;
  padding: 0 15px;
  white-space: nowrap;
  animation: ticker 5s linear infinite;
}

.ticker__icon {
  margin-left: 30px;
}

@keyframes ticker {
  0% {
    transform: translateZ(0);
  }
  to {
    transform: translate3d(-100%, 0, 0);
  }
}
.products {
  margin-top: -1px;
}
.products__top {
  padding: 30px 0;
  align-items: center;
  justify-content: space-between;
}
.products__top::before {
  content: "";
  display: block;
  width: 225px;
  height: 156px;
  background: url("../img/scorpion-left.svg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
@media (max-width: 768px) {
  .products__top::before {
    width: 100px;
    height: 70px;
  }
}
.products__top::after {
  content: "";
  display: block;
  width: 225px;
  height: 156px;
  background: url("../img/scorpion-right.svg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
@media (max-width: 768px) {
  .products__top::after {
    width: 100px;
    height: 70px;
  }
}
.products__title {
  margin: 0 auto;
  max-width: 785px;
  font-weight: 400;
  font-size: 65px;
  line-height: 73px;
  text-align: center;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: var(--black-color);
}
@media (max-width: 1024px) {
  .products__title {
    padding: 20px 0;
  }
}
@media (max-width: 768px) {
  .products__title {
    padding: 20px;
    font-size: 45px;
    line-height: 45px;
  }
}
.products__content {
  position: relative;
  margin-top: -1px;
  padding-top: 20px;
}
.products__decorative {
  position: absolute;
  left: 50%;
  top: 20px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  width: calc(100% - 40px);
  transform: translateX(-50%);
}
.products__decorative svg:nth-child(2) {
  transform: translateX(-15px);
}
.products__decorative svg:nth-child(3) {
  transform: translateX(-15px);
}
@media (max-width: 1024px) {
  .products__decorative {
    display: none;
  }
}
.products__ticker {
  margin-top: -1px;
}
.products__phrase {
  padding: 29px 0;
  font-weight: 400;
  font-size: 17px;
  line-height: 26px;
  text-align: center;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: var(--black-color);
}
.products__list {
  display: flex;
  flex-wrap: wrap;
  padding: 0 20px;
  padding-bottom: 20px;
  gap: 20px;
}
.products__cover {
  padding: 20px;
}
.products__cover img {
  width: 100%;
  object-fit: cover;
}
@media (max-width: 1024px) {
  .products__item {
    width: calc(50% - 13px);
  }
}
@media (max-width: 768px) {
  .products__item {
    width: 100%;
    min-height: 500px;
  }
}

.card {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 22px;
  padding-top: 100px;
  min-height: 658px;
  background-image: url("../img/products-bg.svg");
  background-position: bottom center;
  background-size: cover;
  background-repeat: no-repeat;
}
.card__taste {
  margin-bottom: 14px;
  font-weight: 400;
  font-size: 18px;
  line-height: 28px;
  letter-spacing: 0.5px;
  text-align: center;
  text-transform: uppercase;
  color: var(--black-color);
}
.card__title {
  margin-bottom: 14px;
  font-weight: 400;
  font-size: 32px;
  line-height: 38px;
  letter-spacing: 0.5px;
  text-align: center;
  text-transform: uppercase;
  color: var(--black-color);
}
@media (max-width: 1024px) {
  .card {
    min-height: 700px;
  }
}
@media (max-width: 768px) {
  .card {
    min-height: 500px;
  }
}

.story__block {
  margin-top: -1px;
}
.story__left {
  display: flex;
  flex-direction: column;
  border-right: 1px solid var(--black-color);
  width: 50%;
}
@media (max-width: 1024px) {
  .story__left {
    border: none;
    width: 100%;
  }
}
.story__right {
  width: 50%;
}
@media (max-width: 1024px) {
  .story__right {
    width: 100%;
  }
}
.story__title {
  margin: 0;
  margin-bottom: auto;
  padding: 50px 80px;
  text-align: center;
  font-weight: 400;
  font-size: 58px;
  line-height: 65px;
  text-align: center;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: var(--black-color);
}
@media (max-width: 768px) {
  .story__title {
    padding: 20px;
    font-size: 45px;
    line-height: 45px;
  }
}
.story__image {
  display: block;
}
.story__content {
  padding-top: 135px;
  padding-bottom: 135px;
  background-image: url("../img/story-bg.svg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  aspect-ratio: 1/1;
}
@media (max-width: 1024px) {
  .story__content {
    padding: 170px 0;
  }
}
.story__text {
  margin: 0 auto;
  max-width: 390px;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.5px;
  text-align: center;
  text-transform: uppercase;
  color: var(--black-color);
}
.story__text:not(:last-child) {
  margin-bottom: 24px;
}
@media (max-width: 768px) {
  .story__text {
    padding: 0 15px;
    font-size: 14px;
    line-height: 20px;
  }
}

.ticker-bottom__content {
  font-weight: 400;
  font-size: 17px;
  line-height: 26px;
  text-align: center;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: var(--black-color);
}
.ticker-bottom__content .ticker {
  margin-top: -1px;
  border-right: 1px solid var(--black-color);
  border-left: 1px solid var(--black-color);
  padding: 29px 0;
}
.ticker-bottom__content .ticker__icon {
  margin-right: 30px;
}
.ticker-bottom__content .ticker__icon:last-child {
  margin-right: 0;
}

.banner {
  margin-top: -1px;
}
.banner__image {
  display: block;
  margin-top: -180px;
  width: 100%;
}
@media (max-width: 1024px) {
  .banner__image {
    margin-top: -120px;
  }
}
@media (max-width: 768px) {
  .banner__image {
    margin-top: -45px;
  }
}

.footer__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: -1px;
  padding: 2px 93px;
  background-color: #CDFD3B;
}
@media (max-width: 768px) {
  .footer__content {
    flex-direction: column;
    gap: 20px;
    padding: 20px;
  }
}
.footer__text {
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  text-align: center;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: var(--black-color);
}